
import React from 'react';
import { Segment, Header, Container } from 'semantic-ui-react'

class HeaderComp extends React.Component {
    render() {
        
        return (
            <Container>
            <Segment inverted>
                <Header textAlign="center">
                    Calculator
                </Header>
            </Segment>
            </Container>
        );
    }
}

export default HeaderComp;
